.App {
  text-align: center;
  background-color: rgba(250, 250, 250, 0.968);
  overflow-x: hidden;
  position: relative;
}
.search-form{
  min-height:10vh;
  display:flex;
  justify-content:center;
  align-items: center;
}
.wrapper{
  overflow-x: hidden;
}

@media (min-width:320px) {
  .titleText{
    
    color: black;
    margin-top: 2%;
    font-size: 210%;
    font-weight: 50;
  }
  .search-button{
    background: lightskyblue;
    border: none;
    border-color: black;
    margin-left: 7px;
    border-radius: 4%;
    padding: 7px 12px;
    color: white;
    font-size: 100%;
  }
  .search-bar{
    width: 45%;
    padding: 13px;
    font-size: 70%;
  }
  .state-selector{
    width: 35%;
    margin-right: 1%;
  }
  .radios{
   width: 80%;
  }
}
  
@media (min-width:800px) {
  .titleText{
    color: black;
    margin-top: 2%;
    font-size: 310%;
    font-weight: 50;
  }
  .search-button{
    background: lightskyblue;
    border: none;
    border-color: black;
    margin-left: 7px;
    border-radius: 4%;
    padding: 18px 30px;
    color: white;
    font-size: 150%;
  }
  .search-bar{
    width: 30%;
    padding: 15px;
    font-size: 150%;
  }
  .state-selector{
    width: 15%;
    margin-right: 1%;
  }
  .radios{
    width: 30%;
  }

}

.parks{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  
}

